import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        redirect: '/index' // 重定向
    },
    {
        path: '/index',
        name: 'Index',
        component: () => import('../pages/Index.vue')
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import('../pages/Login/Login.vue')
    },
    {
        path: '/manage',
        name: 'Manage',
        component: () => import('../pages/Manage/Manage.vue'),
        children: [
            {
                path: 'hot',
                component: () => import('../pages/Manage/List/Hot.vue'),
            },
            {
                path: 'pne',
                component: () => import('../pages/Manage/List/Pne.vue'),
            },
            {
                path: 'beijing',
                component: () => import('../pages/Manage/List/Beijing.vue'),
            },
            {
                path: 'yule',
                component: () => import('../pages/Manage/List/Yule.vue'),
            },
            {
                path: 'tiyu',
                component: () => import('../pages/Manage/List/Tiyu.vue'),
            },
            {
                path: 'saishi',
                component: () => import('../pages/Manage/List/Saishi.vue'),
            },
            {
                path: 'keji',
                component: () => import('../pages/Manage/List/Keji.vue'),
            },
            {
                path: 'caijing',
                component: () => import('../pages/Manage/List/Caijing.vue'),
            }
        ]
    }
]

const router = new VueRouter({
    routes
})

export default router;